@tailwind base;
@tailwind components;
@tailwind utilities;

.wrapper svg{
    width: 100%;
}

*{
    scroll-behavior: smooth;

    ::-webkit-scrollbar{
        width: 10px;
        background: rgb(25, 25, 25);
        
      
    }
    ::-webkit-scrollbar-thumb{
        background: rgb(58, 57, 57);
        border-radius: 10px;
        
    }
   
}